import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AuthService } from './../../services/auth.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {
  error:string= '';

  registerForm = new FormGroup({
    firstname: new FormControl (null, [Validators.required, Validators.minLength(3), Validators.maxLength(10)]),
    lastname: new FormControl (null, [Validators.required, Validators.minLength(3), Validators.maxLength(10)]),
    age: new FormControl (null, [Validators.required, Validators.minLength(16), Validators.maxLength(80)]),
    email: new FormControl (null, [Validators.required, Validators.pattern('^[A-Z][a-z0-9]{3,8}$')]),

  })

  constructor(private AuthService:AuthService, private router:Router) { }


  submitRegisterForm(registerForm:FormGroup){
     this.AuthService.register(registerForm.value).subscribe((res)=>{
       if (res.message == 'success') {
           this.router.navigate(['/login'])
       }else{
         this.error = res.errors.email.message
       }
     })
  }


  ngOnInit(): void {
  }

}
